/* Grid container wrapper styling */
.grid-container-wrapper {
  width: 80vw;
  height: 60vh;
  margin: 20px auto;
  border: 1px solid #333;
  position: relative;
  overflow: hidden;
  background: #f0f0f0;
  overscroll-behavior: contain;
}

.grid-container {
  position: relative;
  background: #fff;
}

/* Grid cell styling */
.grid-cell {
  width: 100px;
  height: 100px;
  border: 1px solid #aaa;
  box-sizing: border-box;
  float: left;
  position: relative;
}

.grid-cell.selected {
  background-color: lightgreen;
}

/* Container for dropped vegetable images */
.cell-content {
  width: 100%;
  height: 100%;
}

/* Images in cell content */
.cell-content img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

/* Tooltip styling */
.tooltip {
  /* Inline styles in component */
}

/* Selection overlay styling */
.current-selection.add {
  position: absolute;
  border: 2px dashed green;
  background-color: rgba(0, 255, 0, 0.2);
  pointer-events: none;
  z-index: 10;
}

.current-selection.remove {
  position: absolute;
  border: 2px dashed red;
  background-color: rgba(255, 0, 0, 0.2);
  pointer-events: none;
  z-index: 10;
}

/* Mode toggle styling */
.mode-toggle {
  text-align: center;
  margin-bottom: 10px;
}

/* Dropdown container styling */
.dropdown-container {
  font-size: 14px;
}

.dropdown-group {
  margin-bottom: 5px;
}

.dropdown-group-title {
  font-weight: bold;
  margin-bottom: 2px;
}

.dropdown-item {
  cursor: pointer;
  padding: 2px 0;
}

/* Vegetable list styling */
.vegetable-list {
  position: absolute;
  right: 10px;
  top: 50px;
  width: 150px;
  background: #fff;
  border: 1px solid #ccc;
  z-index: 1000;
  padding: 5px;
}

.vegetable-item {
  display: flex;
  align-items: center;
  cursor: grab;
  margin-bottom: 5px;
}

.vegetable-item img {
  width: 50px;
  height: 50px;
  margin-right: 5px;
}

.grid-cell.drag-over {
  border: 30px solid green;
}

.grid-cell.drag-over-selected {
  border: 4px solid green;
}

.grid-cell.drag-over-not-selected {
  background-color: red;
}

